@import url("https://fonts.googleapis.com/css2?family=Clicker+Script&family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

:root {
  --bg-black-900: #f2f2fc;
  --bg-black-100: #fdf9ff;
  --bg-black-50: #e8dfec;
  --text-black-900: #302e4d;
  --text-black-700: #504e70;
  --skin-color: red;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
}

body {
  line-height: 1.5;
  font-size: 16px;
  font-family: "poppins", sans-serif;
}

::before,
::after {
  box-sizing: border-box;
}

.main {
  display: flex;
  height: 100vh;
}

.main-container {
  background: var(--bg-black-900);
  height: 100vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  position: fixed;
  left: 270px;
  top: 0;
  right: 0;
  z-index: 0;
  opacity: 1;
  padding: 30px;
  overflow-y: scroll;
}

.tasks {
  width: 100%;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.tasks div {
  margin: 0;
}

.task {
  height: 200px;
  width: 200px;
  border: 2px solid black;
}

.aside {
  width: 270px;
  background: var(--bg-black-100);
  position: fixed;
  left: 0;
  top: 0;
  padding: 30px;
  height: 100%;
  z-index: 10;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-right: 1px solid var(--bg-black-50);
  flex-direction: column;
}

.aside .logo {
  font-size: 30px;
  text-transform: uppercase;
  margin-top: -30px;
}

.aside .logo a {
  color: var(--text-black-900);
  font-weight: 700;
  padding: 15px 25px;
  letter-spacing: 5px;
  position: relative;
  text-decoration: none;
}

.aside .logo a span {
  font-family: "Clicker script", cursive;
  font-size: 40px;
}

.aside .logo a::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-bottom: 5px solid var(--skin-color);
  border-left: 5px solid var(--skin-color);
  bottom: 0;
  left: 0;
}

.aside .logo a::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-top: 5px solid var(--skin-color);
  border-right: 5px solid var(--skin-color);
  top: 0;
  right: 0;
}

.aside .create {
  display: flex;
  width: 270px;
  height: 80px;
  justify-content: center;
  align-items: center;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

.aside .create .button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-black-900);
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  margin-left: -10px;
  background: transparent;
  border: none;
  transition: all 0.2s;
  width: 270px;
  height: 80px;
}

.aside .create .button:hover{
  color: white;
  text-shadow: 0.5px 0.5px 3px black, -0.5px 0.5px 3px black,
    0.5px -0.5px 3px black, -0.5px -0.5px 3px black;
  letter-spacing: 1px;
}


.aside .create .button i {
  margin-right: 8px;
  color: red;
  text-shadow: none;
  text-shadow: none;
  
}

.info-parent {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  height: 200px;
  width: 200px;
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  margin-top: 30px;
}

.info .text {
  font-weight: 500;
}

.info .num {
  font-weight: 800;
  color: red;
}

/* Tasks */
.task {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  width: 200px;
  min-height: 200px;
  height: fit-content;
  justify-content: space-evenly;
  align-items: center;
}

.task .check {
  position: absolute;
  color: white;
  font-size: 100px;
  opacity: 1;
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  height: 40px;
}

.middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 130px;
  height: auto;
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 15px;
  padding-bottom: 10px;
  text-align: center;
  border-top: dotted 2px black;
  border-bottom: dotted 2px black;
  width: 100%;
  gap: 20px;
}

.middle h3 {
  font-size: 20px;
  font-weight: 800;
  text-align: center;
  margin: 0;
  font-family: "Poppins", sans-serif;
  text-decoration: underline;
}

.middle p {
  font-size: 12px;
  text-align: center;
  margin: 0;
  word-wrap: break-word;
  word-break: break-all;
  letter-spacing: 0.5px;
  word-spacing: 5px;
  font-family: "Poppins", sans-serif;
}

.modify {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}

.modify .complete {
  width: auto;
  height: 20px;
  font-size: 12px;
  text-align: center;
  padding: 0px 5px;
}

.modify .edit {
  width: auto;
  height: 20px;
  font-size: 12px;
  text-align: center;
  padding: 0px 5px;
}

.top .delete {
  width: 40px;
  height: 40px;
  font-size: 12px;
  text-align: center;
}

.top .delete i {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  border-radius: 100%;
}

.bottom {
  font-size: 13px;
  height: 30px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
}

.bottom span {
  font-size: 12px;
  letter-spacing: 0.5px;
}

.top .delete {
  background-color: transparent;
  width: auto;
  height: auto;
  padding: 0;
  border: none;
  font-size: 20px;
}

.dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  border: 1px solid #ccc;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  width: 400px;
}

.dialog h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.dialog input[type="text"],
.dialog textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.dialog button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

.dialog button:last-child {
  margin-left: 10px;
  background-color: red;
  transition: all 0.5s;
}

.dialog button:last-child:hover {
  background-color: rgb(183, 0, 0);
}

.colors input{
  cursor: pointer;
  display: inline;
  width: 20px;
  height: 20px;
  
}

.colors {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 15px;
  gap: 5px;
}

.blurred-background {
  background-color: rgba(
    255,
    255,
    255,
    0.1
  ); 
  backdrop-filter: blur(5px);
  z-index: 1; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.blur {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
